export function validateInfo(values) {
	let errors = {};
	// console.log("Validating", values);
	if (!values?.fullName?.trim()) {
		errors.fullName = "Name is required";
	}

	if (values?.email) {
		if (!/\S+@\S+\.\S+/.test(values.email)) {
			errors.email = "Email address is invalid";
		}
	}
	if (!values?.phoneNumber) {
		errors.phoneNumber = "Phone Number is required";
	} else if (
		values?.phoneNumber <= 99361000000 ||
		values?.phoneNumber >= 99365999999
	) {
		errors.phoneNumber =
			"Phone Number must be in range of 99361000000 and 99365999999";
	}

	if (!values?.address) {
		errors.address = "Address is required";
	}
	// if (!values?.notes) {
	// 	errors.notes = "Please say something about order";
	// }

	return errors;
}

export function validateProfileInfo(values) {
	let errors = {};
	// console.log("Validating", values);
	if (!values?.username?.trim()) {
		errors.username = "Name is required";
	}

	if (values?.email) {
		if (!/\S+@\S+\.\S+/.test(values.email)) {
			errors.email = "Email address is invalid";
		}
	}
	if (!values?.phoneNumber) {
		errors.phoneNumber = "Phone Number is required";
	} else if (
		values?.phoneNumber <= 99361000000 ||
		values?.phoneNumber >= 99365999999
	) {
		errors.phoneNumber =
			"Phone Number must be in range of 99361000000 and 99365999999";
	}

	if (!values?.address) {
		errors.address = "Address is required";
	}

	return errors;
}

export function validateRegisterInfo(values) {
	let errors = {};
	// console.log("Validating", values);
	if (!values?.username?.trim()) {
		errors.username = "Укажите имя для своего профиля.";
	}
	if (!values.email) {
		errors.email = "Введите адрес электронной почты.";
	}

	if (values?.email) {
		if (!/\S+@\S+\.\S+/.test(values.email)) {
			errors.email = "Введите правильный адрес электронной почты.";
		}
	}

	if (!values?.password) {
		errors.password = "Введите пароль.";
	}
	if (values?.password && values?.password.length < 6) {
		errors.password = "Пароль должен состоять из 6 символов и больше.";
	}
	if (values?.password !== values?.re_password) {
		errors.re_password = "Пароли должны быть одинаковыми.";
	}
	if (!values?.re_password) {
		errors.re_password = "Подтвердите пароль.";
	}

	return errors;
}
