import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { signup } from "../redux/actions/auth";
import { useDispatch } from "react-redux";

import isEmpty from "../utils/isEmpty";
import { validateRegisterInfo } from "../utils/registerValidate";

function Register() {
	const [formData, setFormData] = useState({
		username: "",
		email: "",
		password: "",
		re_password: "",
	});

	const { username, email, password, re_password } = formData;
	const dispatch = useDispatch();
	const [isLoading, setIsLoading] = useState(false);
	const [formErrors, setFormErrors] = useState(null);
	const [startedWriting, setStartedWriting] = useState({
		username: false,
		email: false,
		password: false,
		re_password: false,
	});
	const onChange = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};
	useEffect(() => {
		setFormErrors(validateRegisterInfo(formData));
	}, [formData, startedWriting]);

	const onSubmit = (e) => {
		e.preventDefault();
		setStartedWriting({
			username: true,
			email: true,
			password: true,
			re_password: true,
		});
		setFormErrors(validateRegisterInfo(formData));
		if (isEmpty(formErrors)) {
			setIsLoading(true);
			console.log("Dispatching register");
			dispatch(signup(username, email, password, re_password)).then((res) => {
				setIsLoading(false);
			});
		}
	};

	return (
		<>
			<div className="header d-flex justify-content-center py-5 align-items-center">
				<img src="/images/lofo.svg" width={140} className="img-fluid" />
			</div>
			<div className="social-links mx-auto container">
				<div className=" d-flex flex-column justify-content-center align-items-center">
					<div
						className="h3 fw-bold text-center text-black pt-1 pb-4"
						style={{ fontWeight: 900 }}
					>
						Зарегистрируйтесь и слушайте бесплатно
					</div>
					<button
						className="btn  fw-bold w-90 rounded-pill btn-facebook-2"
						style={{ fontSize: "16px", padding: "12px 32px" }}
					>
						Зарегистрироваться через Facebook
					</button>
				</div>
				<div
					class="divider my-4 text-gray"
					style={{ fontSize: "14px", fontWeight: 500, letterSpacing: "1px" }}
				>
					или
				</div>
				<div className="h6 fw-bold text-black text-center">
					Укажите электронную почту и пароль
				</div>
				<div className="form py-3 pb-5 mb-3 ">
					<div class="mb-3 has-validation">
						<label
							for="exampleFormControlInput1"
							class="form-label"
							style={{ fontSize: "14px" }}
						>
							<strong>Ваш адрес электронной почты</strong>
						</label>
						<input
							type="email"
							style={{
								borderRadius: "4px",
								fontSize: "16px",
								lineHeight: 1.5,
								color: "#222326",
							}}
							name="email"
							value={email}
							onChange={(e) => {
								onChange(e);
								setStartedWriting({ ...startedWriting, email: true });
							}}
							className={`form-control  py-3 ${
								startedWriting.email === true &&
								(formErrors?.email ? "is-invalid" : "is-valid")
							}`}
							id="exampleFormControlInput1"
							placeholder="Введите адрес электронной почты."
						/>
						<div
							class="invalid-feedback my-2"
							style={{ fontWeight: 500, lineHeight: 1.5 }}
						>
							{formErrors?.email}
						</div>
					</div>

					<div class="mb-3 has-validation">
						<label
							for="exampleFormControlInput3"
							style={{ fontSize: "14px" }}
							class="form-label"
						>
							<strong>Придумайте пароль</strong>
						</label>
						<input
							type="password"
							style={{
								borderRadius: "2px",
								fontSize: "16px",
								lineHeight: 1.5,
								color: "#222326",
							}}
							name="password"
							value={password}
							onChange={(e) => {
								onChange(e);
								setStartedWriting({ ...startedWriting, password: true });
							}}
							className={`form-control  py-3 ${
								startedWriting.password === true &&
								(formErrors?.password ? "is-invalid" : "is-valid")
							}`}
							id="exampleFormControlInput3"
							placeholder="Придумайте пароль"
						/>
						<div
							class="invalid-feedback my-2"
							style={{ fontWeight: 500, lineHeight: 1.5 }}
						>
							{formErrors?.password}
						</div>
					</div>
					<div class="mb-3 has-validation">
						<label
							for="exampleFormControlInput4"
							style={{ fontSize: "14px" }}
							class="form-label"
						>
							<strong>Подтвердите пароль</strong>
						</label>
						<input
							type="password"
							style={{
								borderRadius: "2px",
								fontSize: "16px",
								lineHeight: 1.5,
								color: "#222326",
							}}
							name="re_password"
							value={re_password}
							onChange={(e) => {
								onChange(e);
								setStartedWriting({ ...startedWriting, re_password: true });
							}}
							className={`form-control  py-3 ${
								startedWriting.re_password === true &&
								(formErrors?.re_password ? "is-invalid" : "is-valid")
							}`}
							id="exampleFormControlInput4"
							placeholder="Подтвердите пароль."
						/>
						<div
							class="invalid-feedback my-2"
							style={{ fontWeight: 500, lineHeight: 1.5 }}
						>
							{formErrors?.re_password}
						</div>
					</div>
					<div class="mb-3 has-validation">
						<label
							for="exampleFormControlInput5"
							class="form-label"
							style={{ fontSize: "14px" }}
						>
							<strong>Ваше имя</strong>
						</label>
						<input
							type="text"
							style={{
								borderRadius: "4px",
								fontSize: "16px",
								lineHeight: 1.5,
								color: "#222326",
							}}
							name="username"
							value={username}
							onChange={(e) => {
								onChange(e);
								setStartedWriting({ ...startedWriting, username: true });
							}}
							className={`form-control  py-3 ${
								startedWriting?.username === true &&
								(formErrors?.username ? "is-invalid" : "is-valid")
							}`}
							id="exampleFormControlInput5"
							placeholder="Укажите свое имя"
							onKeyDown={(e) => {
								if (e.key === "Enter") {
									onSubmit(e);
								}
							}}
						/>
						<div
							class="invalid-feedback my-2"
							style={{ fontWeight: 500, lineHeight: 1.5 }}
						>
							{formErrors?.username}
						</div>
					</div>
					<p
						className="text-center text-black mt-5"
						style={{ fontSize: "12px" }}
					>
						Нажимая «Зарегистрироваться», вы принимаете{" "}
						<Link to="/" className="text-green">
							Условия использования Spotify
						</Link>
						.
					</p>
					<p className="text-center text-black" style={{ fontSize: "12px" }}>
						Чтобы узнать больше о том, как Spotify собирает, использует,
						передает и защищает персональные данные, прочитайте нашу{" "}
						<Link to="/" className="text-green">
							Политику конфиденциальности
						</Link>
						.
					</p>

					<div className="d-flex justify-content-center">
						<button
							type="submit"
							onClick={(e) => onSubmit(e)}
							disabled={!isEmpty(formErrors)}
							className="btn  mx-auto fw-bold my-2 w-50 py-3 rounded-pill btn-register"
							style={{ fontSize: "16px" }}
						>
							{isLoading ? (
								<div class="spinner-border text-white" role="status">
									<span class="visually-hidden">Loading...</span>
								</div>
							) : (
								<>Зарегистрироваться</>
							)}
						</button>
					</div>
					<div
						className="text-center text-black py-3 "
						style={{ fontSize: "16px" }}
					>
						Уже есть аккаунт?{" "}
						<Link to="/login" className="text-green">
							Войти
						</Link>
						.
					</div>
				</div>
			</div>
		</>
	);
}

export default Register;
